.img_popup {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10000;
  text-align: center;
  line-height: 0;
  font-weight: normal;
}
.img_popup a img {
  border: none;
}
.img_popup .img_popup-image {
  display: block;
  height: auto;
  max-width: inherit;
  max-height: none;
  border-radius: 3px;
  border: 4px solid white;
}

.img_popup-overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: black;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  opacity: 0.8;
  display: none;
}

.img_popup-outer_container {
  position: relative;
  *zoom: 1;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  border-radius: 4px;
  background-color: white;
}
.img_popup-outer_container:after {
  content: "";
  display: table;
  clear: both;
}

.img_popup-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
}

.img_popup-cancel {
  display: block;
  margin: 0 auto;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 5px solid #ccc;
  border-top-color: #333;
  animation: spinner 1.5s linear infinite;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
.img_popup-data_container {
  margin: 0 auto;
  padding-top: 5px;
  *zoom: 1;
  width: 100%;
  -moz-border-radius-bottomleft: 4px;
  -webkit-border-bottom-left-radius: 4px;
  border-bottom-left-radius: 4px;
  -moz-border-radius-bottomright: 4px;
  -webkit-border-bottom-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.img_popup-data_container:after {
  content: "";
  display: table;
  clear: both;
}

.img_popup-data {
  padding: 0 4px;
  color: #ccc;
}
.img_popup-data .img_popup-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}
.img_popup-data .img_popup-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}
.img_popup-data .img_popup-caption a {
  color: #4ae;
}
.img_popup-data .img_popup-close {
  position: relative;
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  text-align: right;
  outline: none;
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.img_popup-data .img_popup-close::before, .img_popup-data .img_popup-close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4px;
  height: 30px;
  background: #fff;
}
.img_popup-data .img_popup-close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.img_popup-data .img_popup-close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.img_popup-data .img_popup-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}